.header{width:75%;margin:30px 10%;padding:15px;border:3px solid var(--HeaderOutline);border-radius:5px;display:flex;justify-content:space-between;align-items:center}.logo{height:60px}.score{padding:10px 30px;background:#fff;color:var(--DarkText);font-weight:700;border-radius:5px;display:flex;flex-direction:column;align-items:center}.score-text{margin:0;font-size:11px;letter-spacing:2px;color:var(--ScoreText)}.score-number{margin:0;font-size:40px;font-family:"Barlow Bold"}@media screen and (min-width: 1366px){.header{width:900px;border-radius:15px}.logo{height:100px}.score{padding:20px 40px}}