.rules{position:absolute;height:100%;width:100%;background-color:rgba(0,0,0,.5)}.rules-wrapper{height:100%;width:100%;background-color:#fff;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.close{cursor:pointer}.title-rules{font-size:30px;font-family:"Barlow Bold";color:var(--DarkText)}@media screen and (min-width: 1366px){.rules-wrapper{position:absolute;height:auto;width:auto;padding:30px;border-radius:15px;top:50%;left:50%;transform:translate(-50%, -50%);align-items:flex-start}.img-rules{margin:40px 25px}.close{position:absolute;top:40px;right:40px}}